const Instructions = (props) => {
  return (
    <section className="instructions">
      <p>
        Bienvenido al juego del ahorcado. Tu objetivo es adivinar la palabra oculta antes de que el dibujo del ahorcado se complete.
      </p>
      <p>
        Para jugar, introduce una letra en el campo de entrada. Si la letra est en la palabra, aparecer en su posicin correspondiente. Si la letra no est en la palabra, se aadir a la lista de errores y se dibujar una parte del ahorcado.
      </p>
      <p>
        El juego termina cuando logras adivinar la palabra o cuando se completa el dibujo del ahorcado. Buena suerte!
      </p>
    </section>
  );
};

export default Instructions;